import React, { useContext, useEffect, useState } from "react";

import profileImg from "../assets/profile.webp"

// import "../styles/page.css"
import "../styles/header.css"
import logo from '../assets/logo.png'
import { NavLink, Link } from "react-router-dom";
import { ProfileContext } from "../App";



export default function Header() {
  const {globalProfileData, setGlobalProfileData} = useContext(ProfileContext);

  const [isTokenExist, setIsTokenExist] = useState(false)
  

  useEffect(() => {

    const timeOut = setTimeout(() => {
      const token = localStorage.getItem('access_token')
      if (token) {
        setIsTokenExist(true)
      }else{
        setIsTokenExist(false)
      }
    }, 500)

    return () => clearTimeout(timeOut)

  })

  return <header>
    <div className="page">
      <Link to="/"><img className="logo" src={logo} /></Link>
      <div className="center">
        <NavLink to="/my-chatbot">My Chatbot</NavLink>
        <NavLink to="/preview">Preview</NavLink>
        <NavLink to="/invite">Invite</NavLink>
        <NavLink to="/pricing">Pricing</NavLink>
        {/* <NavLink to="/integrates">Integrates</NavLink> */}
      </div>

      <div className="right">
        {isTokenExist && <NavLink className="profile-img" to={"/my-profile"}>
          <img src={globalProfileData.image} />
        </NavLink>}
        {isTokenExist ? <button className="hover simple"><NavLink to="/my-chatbot">Manage chatbot
          <svg width="20" height="20" fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M13.704 4.284a1 1 0 1 0-1.403 1.424L17.67 11H4a1 1 0 1 0 0 2h13.665L12.3 18.285a1 1 0 0 0 1.403 1.424l6.925-6.822a1.25 1.25 0 0 0 0-1.78l-6.925-6.823Z" fill="rgb(56, 56, 56)" /></svg></NavLink></button> : <button className="hover login"><NavLink to="/login">Login/SignUp</NavLink></button>}
      </div>
    </div>
  </header>
}