import React, { useContext, useState } from "react";
import "../../styles/CreateChatbot/common.css";
import "../../styles/Dialog.css";
import "../../styles/CreateChatbot/extra-tab-common.css";
import logo from "../../assets/logo.png";
import whatsapp from "../../assets/whatsapp_logo.png";
import wordpress from "../../assets/wordpress_logo.png";
import shopify from "../../assets/shopify_logo.png";
import messenger from "../../assets/facebook_messenger.png";
import WhatsAppDialog from "../Dialog/WhatsAppDialog";
import FacebookDialog from "../Dialog/FacebookDialog";
import { ServerDataContext } from "../../pages/MyChatbot/UpdateChatbot";

export default function Integrates() {

  const { serverData, setServerData } = useContext(ServerDataContext)

  const [isWhatsapp, setIsWhatsapp] = useState(false)
  const [isFacebook, setIsFacebook] = useState(false)

  function onWhatsappDialogClose() {
    setIsWhatsapp(false)
  }

  function onFacebookDialogClose() {
    setIsFacebook(false)
  }

  return (
    <>
      <div className="main-content-holder small integrates">
        <div className="header">
          <img className="icon" src={logo} alt="logo" />
        </div>

        <div className="content">
          <button className="tab-button">
            <img src={wordpress} />
            <span className="responsive-hide">Add to Wordpress</span>
            {serverData.integrate_details.is_wordpress_integrated ? <span className="default blue">Added</span> : <span className="default green">Coming Soon</span>}
          </button>

          <button className="tab-button">
            <img src={shopify} />

            <span className="responsive-hide">Add to Shopify</span>
            {serverData.integrate_details.is_shopify_integrated ? <span className="default blue">Added</span> : <span className="default green">Coming Soon</span>}
          </button>

          <button className="tab-button">
            <img src={whatsapp} />
            <span className="responsive-hide">Add to Whatsapp</span>
            {serverData.integrate_details.is_whatsapp_integrated ? <span className="default blue hover" onClick={() => setIsWhatsapp(true)}><svg width="16" height="16" fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M13.94 5 19 10.06 9.062 20a2.25 2.25 0 0 1-.999.58l-5.116 1.395a.75.75 0 0 1-.92-.921l1.395-5.116a2.25 2.25 0 0 1 .58-.999L13.938 5Zm7.09-2.03a3.578 3.578 0 0 1 0 5.06l-.97.97L15 3.94l.97-.97a3.578 3.578 0 0 1 5.06 0Z" /></svg> Added</span> : <span className="default  hover" onClick={() => setIsWhatsapp(true)}>Add</span>}
          </button>

          <button className="tab-button">
            <img src={messenger} />
            <span className="responsive-hide">Add to Facebook Messenger</span>

            {serverData.integrate_details.is_facebook_integrated ? <span className="default blue hover" onClick={() => setIsFacebook(true)}><svg width="16" height="16" fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M13.94 5 19 10.06 9.062 20a2.25 2.25 0 0 1-.999.58l-5.116 1.395a.75.75 0 0 1-.92-.921l1.395-5.116a2.25 2.25 0 0 1 .58-.999L13.938 5Zm7.09-2.03a3.578 3.578 0 0 1 0 5.06l-.97.97L15 3.94l.97-.97a3.578 3.578 0 0 1 5.06 0Z" /></svg> Added</span> : <span className="default hover" onClick={() => setIsFacebook(true)}>Add</span>}
          </button>
        </div>
      </div>
      <WhatsAppDialog isOpen={isWhatsapp} onDialogClose={onWhatsappDialogClose} defaultIndex={serverData.integrate_details.is_whatsapp_integrated ? 2 : 0} />
      <FacebookDialog isOpen={isFacebook} onDialogClose={onFacebookDialogClose} defaultIndex={serverData.integrate_details.is_facebook_integrated ? 2 : 0} />
    </>
  );
}
