import dotIcon from '../assets/dot.png'
import mobileImage from '../assets/mobile.png'
import VideoAndFeatures from '../components/HomePage/VideoAndFeatures'
import { Link } from 'react-router-dom'
import ChatBoxIframe from '../components/ChatBoxIframe'
import IntegrateSection from '../components/HomePage/Integration'
import { Helmet } from 'react-helmet'


function Home() {

  const isUserExist = localStorage.getItem("access_token")

  return (
    <>
      <Helmet>
        <script>
          const urbanChatbotId = "1840ba95-be8f-47ec-a25c-47c53d9bd833"
        </script>
        <script src="https://urbanchat.ai/integration/chatbot-integration.js"></script>
      </Helmet>
      <Landing />
      <FeatureSection />
      <IntegrateSection />
      <VideoAndFeatures />

      <div className='page'>
        <br />
        <br />
        <div>
          <div style={{ maxWidth: 1000, marginLeft: 'auto', marginRight: 'auto' }}>
            <div className='home-chat'>
              <img src={dotIcon} />
              <h2>Live Demo</h2>
            </div>
            <p className='home-chat-text'>This chatbot was trained on a document explaining Urbanchat.</p>
            <p className='home-chat-text'>UrbanChat You can embed a widget like this on any page on your website....!</p>
            <br />
            <br />

            <ChatBoxIframe defaultId={"1840ba95-be8f-47ec-a25c-47c53d9bd833"} isSmall={true} />
          </div>

        </div>
      </div>

      <section className='grow-business'>
        <div className='page'>
          <div>
            <h2>GROW YOUR BUSINESS WITH URBANCHAT.AI <br /> <span>TRY NOW FOR FREE!</span></h2>
            <Link to={isUserExist ? "/my-chatbot/create" : "/login"}><button className="hover">Create Your Chatbot <svg width="20" height="20" fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M13.704 4.284a1 1 0 1 0-1.403 1.424L17.67 11H4a1 1 0 1 0 0 2h13.665L12.3 18.285a1 1 0 0 0 1.403 1.424l6.925-6.822a1.25 1.25 0 0 0 0-1.78l-6.925-6.823Z" fill="#fff" /></svg></button></Link>

          </div>
          <img src={mobileImage} />


        </div>
      </section>


      {/* <HeroSection/>
      <HeroSectionDup />
      <HeroCarousel />
      <AiChart />
      <ChatFeatures />
       <LiveDemo />
      <ChatBox/>
      <Tools />
      <div className={classes.faqHome}>
        <FAQ />
      </div> */}
    </>
  )
}

export default Home