import React, { useEffect, useState } from "react";
import { HexColorPicker } from "react-colorful";
import "../../styles/Dialog.css"

export default function ColorPickDialog({ isDialogOpen, onDialogClose, onColorPick }) {

    const [isOpen, setIsOpen] = useState(false)
    const [color, setColor] = useState("#aabbcc");

    useEffect(() => setIsOpen(isDialogOpen), [isDialogOpen])

    function close(className) {
        if (className == 'dialog-outer') {
            onDialogClose()
        }
    }


    useEffect(() => {

        if(onColorPick) onColorPick(color)
    }, [color])


    return <>
        {isOpen && <div className="dialog-outer"  onClick={(e) => close(e.target.className)}>
            <div className="">
                <HexColorPicker color={color} onChange={setColor} />
            </div>
        </div>}
    </>;
}