import React, { useEffect, useRef, useState } from "react";

import videoAnim from '../../assets/video-anim.mp4'
import videoAnim2 from '../../assets/video-anim2.mp4'
import videoAnim3 from '../../assets/video-anim3.mp4'
import customizeImage from '../../assets/image2.png'
import integrateImage from '../../assets/image4.png'
import testImage from '../../assets/test.jpg'
import leadImage from '../../assets/image1.png'
import videoImage from '../../assets/video-img.png'
import '../../styles/HomePage/video-features.css'
import { Link } from "react-router-dom";


export default function VideoAndFeatures() {
    const isUserExist = localStorage.getItem("access_token")

    const [activeIndex, setActiveIndex] = useState(false)

    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }



    const videoItem = useRef()
    const videoItem2 = useRef()
    const attemptPlay = () => {
        videoItem &&
            videoItem.current &&
            videoItem.current.play().catch(error => {
                console.error("Error attempting to play", error);
            });

        videoItem2 &&
            videoItem2.current &&
            videoItem2.current.play().catch(error => {
                console.error("Error attempting to play", error);
            });
    };

    useEffect(() => {
        attemptPlay();
    }, []);


    function changeIndex(index) {
        setActiveIndex(index)
    }







    useEffect(() => {

        window.addEventListener('scroll', onScroll)

        return () => {
            window.removeEventListener('scroll', onScroll)
        }
    }, [])




    function onScroll() {
        const top = window.scrollY
        const itemPos = document.querySelector("#video-feat").offsetTop - window.innerHeight + window.innerHeight / 10
        const parentHeight = document.querySelector("#auto-popup")
        const item = document.querySelector("#popup-ele2>a")
        // console.log(top, itemPos, parentHeight.clientHeight)

        if (top > itemPos && top < (itemPos + parentHeight.clientHeight)) {
            item.classList.add("animate")
            // item.classList.remove("animate-rev")
        } else {
            item.classList.remove("animate")
            // item.classList.add("animate-rev")
        }
    }




    return <section className="video-feature" id="video-feat">

        <div id="auto-popup" className="auto-popup">
            <button id="popup-ele2">
                <Link to={isUserExist? "/my-chatbot/create" : "/login"}>
                    <span>Create your chatbot</span>
                    <div className="icon">
                        <svg xmlns="http://www.w3.org/2000/svg" width="23" height="14" viewBox="0 0 23 16" fill="none">
                            <path d="M2 8H19" stroke="#F4F5F7" strokeWidth="3" strokeLinecap="round" />
                            <path d="M15 2L21 8L15 14" stroke="#F4F5F7" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" />
                        </svg>
                    </div>
                </Link>
            </button>
        </div>

        <div className="page">
            <div className="video-container">
                <div className="center">
                    {/* <img style={{ width: "85%", display: "block", margin: "auto" }} src={videoImage} /> */}
                    <video style={{ width: "100%", display: "block", margin: "auto" }} playsInline muted ref={videoItem} loop src={videoAnim3} autoPlay />
                </div>
                {/* <div className="top">
                    <video className="bottom-left" playsInline muted ref={videoItem} loop src={videoAnim} autoPlay />
                    <video className="top-right" playsInline muted ref={videoItem2} loop src={videoAnim2} autoPlay />
                </div> */}

            </div>


            <div className="fea">
                <div className="left">
                    <div className={activeIndex == 0 ? "active hover" : "hover"} onClick={() => changeIndex(0)}>
                        <h3>Customize</h3>
                        <p>Choose the features you need and tailor the chatbot's responses to align with your brand's voice.</p>
                    </div>
                    <div className={activeIndex == 1 ? "active hover" : "hover"} onClick={() => changeIndex(1)}>
                        <h3>Integrate</h3>
                        <p>Deploy your chatbot across  platforms - Facebook Messenger, Shopify, or WordPress.</p>
                    </div>
                    <div className={activeIndex == 2 ? "active hover" : "hover"} onClick={() => changeIndex(2)}>
                        <h3>Anaylize and Test</h3>
                        <p>Anaylize and test your chatbot.</p>
                    </div>
                    <div className={activeIndex == 3 ? "active hover" : "hover"} onClick={() => changeIndex(3)}>
                        <h3>Collect Leads</h3>
                        <p>Effortlessly collect leads as your chatbot engages with users, turning casual interactions into potential business opportunities.</p>
                    </div>
                </div>
                <div className="right">
                    {activeIndex == 0 && <img src={customizeImage} />}
                    {activeIndex == 1 && <img src={integrateImage} />}
                    {activeIndex == 2 && <img src={testImage} />}
                    {activeIndex == 3 && <img src={leadImage} />}

                </div>
            </div>

            <video className="phone" playsInline muted ref={videoItem} loop src={videoAnim} autoPlay />
        </div>


    </section>
}