import React, { useEffect, useRef } from "react";
import '../../styles/HomePage/featureSection.css'
import integrationImage from '../../assets/integration.png'
import integrationImage2 from '../../assets/integration2.png'
import chatImage from '../../assets/chat-on-hand.png'
import { Link } from "react-router-dom";
import IntegrateSection from '../HomePage/Integration'



export default function FeatureSection() {

    const isUserExist = localStorage.getItem("access_token")

    useEffect(() => {

        window.addEventListener('scroll', onScroll)

        return () => {
            window.removeEventListener('scroll', onScroll)
        }
    }, [])




    function onScroll() {
        const top = window.scrollY
        const itemPos = document.querySelector("#first-feat").offsetTop - window.innerHeight + window.innerHeight / 10
        const parentHeight = document.querySelector(".auto-popup")
        const item = document.querySelector("#popup-ele>a")
        // console.log(top, itemPos, parentHeight.clientHeight)

        if (top > itemPos && top < (itemPos + parentHeight.clientHeight)) {
            item.classList.add("animate")
            // item.classList.remove("animate-rev")
        } else {
            item.classList.remove("animate")
            // item.classList.add("animate-rev")
        }
    }

    
    return <>
        <section id="first-feat" className="feature-section">
            <div className="auto-popup">
                <button id="popup-ele">
                    <Link to={isUserExist? "/my-chatbot/create" : "/login"}>
                        <span>Create your chatbot</span>
                        <div className="icon">
                            <svg xmlns="http://www.w3.org/2000/svg" width="23" height="14" viewBox="0 0 23 16" fill="none">
                                <path d="M2 8H19" stroke="#F4F5F7" strokeWidth="3" strokeLinecap="round" />
                                <path d="M15 2L21 8L15 14" stroke="#F4F5F7" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" />
                            </svg>
                        </div>
                    </Link>
                </button>

            </div>
            <div className="page first">
                <div className="bottom">
                    <div className="content dot">
                        <h2>What's Holding Your <span>Business</span> Back?</h2>

                        <div>
                            <p>Too Many Customer inquiries?</p>
                        </div>
                        <div>
                            <p>Is Your Customer Service Not Good Enough?</p>
                        </div>
                        <div>
                            <p>Spending Too Much on Staff?</p>
                        </div>
                        <div>
                            <p>Need Help Answering Customers Anytime?</p>
                        </div>
                        <div>
                            <p>Your Business Growing Fast?</p>
                        </div>
                        <div>
                            <p>Want to Talk to Customers in a Better Way?</p>
                        </div>
                    </div>
                    <img src={chatImage} />
                </div>
            </div>

            <div className="page">
                <div className="center">
                    <img src={integrationImage} />
                    <div className="content">

                        <h2>Integrate AI into your <span>Business</span></h2>

                        <div>
                            <p>Whatsapp Business - Real-time customer service and sales</p>
                        </div>
                        <div>
                            <p>Facebook Messenger - Engage your social audience instantly.</p>
                        </div>
                        <div>
                            <p>Shopify - Streamline e-commerce interactions.</p>
                        </div>
                        <div>
                            <p>Wordpress - Enhance your website's user experience.</p>
                        </div>
                    </div>
                </div>

            </div>
            <div className="page last">
                <div className="bottom">
                    <div className="content">
                        <h2>Transforming the Way You Connect with <span>Customers</span></h2>

                        <div>
                            <p><b>Increase Sales</b> - Seamlessly turn conversations into sales with AI efficiency.</p>
                        </div>
                        <div>
                            <p><b>Maximize Leads</b> -  Capture leads instantly with engaging, real-time chat.</p>
                        </div>
                        <div><p><b>Reduce Operational Costs</b> - Drastically cut service costs with smart AI automation.</p></div>
                        <div>
                            <p><b>Global Reach</b> - Communicate effortlessly in any language with AI translation.</p>
                        </div>
                    </div>
                    <img src={integrationImage2} />
                </div>
            </div>
        </section>
    </>
}


