
import React from 'react';
import ChatBoxIframe from '../components/ChatBoxIframe';
import "../styles/preview-page.css";


function Preview() {
    return (
        <div className='preview-page'>
            <div className='page'>
                <h2 >Preview</h2>
                <p>This chatbot was trained on a document explaining UrbanChat, You can embed a widget like this on any page on your website!</p>
                <br />
                <br />
                <div style={{ maxWidth: 850, marginLeft: "auto", marginRight: "auto" }}>
                    <ChatBoxIframe defaultId={"1840ba95-be8f-47ec-a25c-47c53d9bd833"} isSmall={true} />
                </div>
            </div>
        </div>
    );
}

export default Preview;