import React, { useEffect, useState } from "react";

import Dropdown from "../../Widget/Dropdown";
import { fetchLeadsHistory } from "../../../lib/api/ApiUpdateChatbot";
import { useParams } from "react-router-dom";
import toast from "react-hot-toast";
import { getIsoTime } from "../../../Utils";

export default function Leads() {


  const [page, setPage] = useState(1)
  const [isOver, setIsOver] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [results, setResults] = useState([])

  const { id } = useParams()



  async function loadLeads(startTime, endTime) {
    if (isOver || isLoading) return
    setIsLoading(true)

    const res = await fetchLeadsHistory(id, startTime, endTime)
    if (res.data) {
      setResults(res.data.leads)
      console.log(res.data)
      toast.success('Fetched successfully')
    } else {
      toast.error(res.message)
    }

    setIsLoading(false)

  }


  function onSubmit(event) {
    event.preventDefault()
    const start = event.target.querySelector('#start-date').value
    const end = event.target.querySelector('#end-date').value

    loadLeads(getIsoTime(new Date(start)), getIsoTime(new Date(end)))

  }


  // 2023-01-01T00:00:00

  return (
    <div>
      <div>
        <h2>Leads</h2>


        <form onSubmit={(event) => onSubmit(event)} className="grid-auto-max-max" style={{alignItems: "end"}}>
          <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: "24px" }}>
            <div>
              <p className="input-title">Start Date</p>
              <input id="start-date" name="start" required type="date" />
            </div>
            <div>
              <p className="input-title">End Date</p>
              <input id="end-date" name="end" required type="date" />
            </div>

          </div>
          <div className="btn-holder">
            <button className="hover" type="submit">View History</button>
            <button className="gray hover" type="button">Download History</button>
          </div>
        </form>

        <br />
        <br />
        <br />
        <h2>History</h2>
        <div className="chat-area">
          {results.map((item, index) => <div className="chat" key={index}>
            <p>{item}</p>
          </div>)}

        </div>
      </div>
    </div>
  );
}
