import React, { useEffect, useRef, useState } from "react";

import Dropdown from "../../Widget/Dropdown";
import { fetchChatbotHistory } from "../../../lib/api/ApiUpdateChatbot";
import { useParams } from "react-router-dom";
import toast from "react-hot-toast";
import { getIsoTime } from "../../../Utils";

export default function ChatHistory() {

  const { id } = useParams();

  const [chats, setChats] = useState([])
  const page = useRef(1)

  const [isLoading, setIsLoading] = useState(true)

  async function loadChatHistory(page, startDate, endDate) {

    setIsLoading(true)

    const res = await fetchChatbotHistory(id, page, startDate, endDate)

    if (res.data) {
      const chatHistory = res.data.chatHistory
      const tempChats = []
      for (const iterator of chatHistory) {
        tempChats.push({
          chat: iterator.user_msg,
          type: "user"
        })

        tempChats.push({
          chat: iterator.chatbot_reply,
          type: "bot"
        })
      }

      setChats((old) => [...old, ...tempChats])

    } else {
      toast.error(res.message)
    }

    setIsLoading(false)
  }

  function onSubmit(event) {
    event.preventDefault()
    const start = event.target.querySelector('#start-date').value
    const end = event.target.querySelector('#end-date').value

    page.current = 1
    setChats([])
    loadChatHistory(page.current, getIsoTime(new Date(start)), getIsoTime(new Date(end)))

  }


  return (
    <div>
      <div>
        <h2>Chat History</h2>

        <form onSubmit={(event) => onSubmit(event)} >

          <div className="flex-wrap">
            <div>
              <p className="input-title">Start Date</p>
              <input id="start-date" name="start" required type="date" />
            </div>

            <div>
              <p className="input-title">End Date</p>
              <input id="end-date" name="end" required type="date" />
            </div>

            <div>
              <p className="input-title">Confidence Score</p>
              <Dropdown items={["Less than 2.5"]} />
            </div>

            <div>
              <p className="input-title">Sources</p>
              <Dropdown items={["API"]} />
            </div>
          </div>

          <div className="flex-wrap">
            <button className="gray hover" type="button">Download History</button>
            <button className="hover" type="submit">View History</button>
          </div>

        </form>

        <br />
        <h2>History</h2>
        <div className="chat-area">
          {/* <div className="chat">
            <p> Hi...! I am UrbanchatAI, ask me anything about Urbanchat!</p>
          </div> */}

          {chats.map((item) => <div className={"chat " + (item.type == "user" ? "primary" : "")}>
            <p>{item.chat}</p>
          </div>)}

        </div>
      </div>
    </div>
  );
}
