import React from "react";
import '../../styles/HomePage/landing.css'
// import '../../styles/page.css'
import robotImage from '../../assets/robot.png';
import chatImage from '../../assets/chat-img.svg';
import gradient from '../../assets/gradiant.svg';
import { Link } from "react-router-dom";


export default function Landing() {
    const isUserExist = localStorage.getItem("access_token")

    return <section className="landing">
        <div className="page">
            <div className="left">
                <h1>The Ultimate Ai Chat<span>bot</span> <span>Solution</span></h1>
                <p>Get a Custom ChatGPT for your website and unlock the Power of AI Chatbots for Your Business. Transform Customer Engagement, Boost Sales, and Streamline Support with UrbanChat.ai</p>
                <Link to={isUserExist? "/my-chatbot/create" : "/login"} className="hover"><button>Create Your Chatbot <svg width="20" height="20" fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M13.704 4.284a1 1 0 1 0-1.403 1.424L17.67 11H4a1 1 0 1 0 0 2h13.665L12.3 18.285a1 1 0 0 0 1.403 1.424l6.925-6.822a1.25 1.25 0 0 0 0-1.78l-6.925-6.823Z" fill="#fff" /></svg></button></Link>
            </div>
            <div className="right">
                <img className="gradient" src={gradient} />
                <img className="bot" src={robotImage} />
            </div>
        </div>
    </section>
}